.contacts {
    background: var(--background-color);
    padding-top: 60px;
    padding-bottom: 60px;
}

.contacts__list {

    list-style: none;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 16px;
}

// .contacts__item { 

// }

.contacts-card {
    padding: 40px 0px 24px 32px;

    width: 280px;
    height: 412px;
    border-radius: 24px;
    background: var(--primary-text-color);
    box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
    border-radius: 24px;
}

.contacts-card__category {
    width: 80px;
    height: 22px;
    padding: 4px 22px 4px;


    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 12px;
    line-height: calc(14 / 12);


    align-items: center;
    text-align: center;
    letter-spacing: 0.04em;

    color: var(--secondary-text-color);
    background: var(--accent-color);

}

.contacts-card__category:nth-child(2) {
    width: 130px;
}

.contacts-card__city {

    margin-top: 20px;
    margin-bottom: 8px;
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 12px;
    line-height: calc(22 / 12);


    align-items: left;
    letter-spacing: 0.04em;

    color: var(--secondary-text-color);
}


.contacts-card__address {
    width: 190px;
    height: 40px;
    padding-bottom: 24px;
    font-family: 'Montserrat';
    font-style: inherit;
    font-weight: 500;
    font-size: 12px;
    line-height: calc(22 / 12);

    letter-spacing: 0.04em;

    color: var(--products-color-third);

}

.contacts-card__container {
    padding-bottom: 24px;
    width: 216px;
    border-bottom: 1px solid var(--border-solid-color);
}

.contacts-card__working {
    padding-top: 24px;
    padding-bottom: 24px;
    margin-bottom: 24px;
    width: 216px;
    border-bottom: 1px solid var(--border-solid-color);
}

.contacts-card__working-days {
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 12px;
    line-height: calc(22 / 12);


    color: var(--secondary-text-color);
}

.contacts-card__working-hours {
    display: flex;
    flex-direction: column;
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 12px;
    line-height: calc(22 / 12);


    color: #ABABAB;
}

.contacts-card__working-hours:not(:last-child) {
    margin-bottom: 12px;
}

.contacts-card__phone {
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 16px;
    line-height: calc(24 / 16);

    color: var(--secondary-text-color);
}

.contacts-card__mail {
    font-family: 'Montserrat';
    font-weight: 500;
    font-size: 16px;
    line-height: calc(24 / 16);

    color: var(--secondary-text-color);
}

.contacts__button {
    display: block;
    margin: 20px auto 16px;
    width: 196px;
    height: 42px;
    background-color: var(--background-color);
    text-align: center;
    align-items: center;
    justify-content: center;
    border: 2px solid var(--accent-color);
    border-radius: 10px;

    transition-property: background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        background-color: var(--accent-color);
        color: var(--secondary-text-color);
    }
}

.contacts__franchise-button {
    display: block;
    margin: 0 auto;
    width: 196px;
    height: 42px;
    background-color: var(--background-color);
    text-align: center;
    align-items: center;
    justify-content: center;
    border: 2px solid var(--accent-color);
    border-radius: 10px;

    transition-property: background-color;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        background-color: var(--accent-color);
        color: var(--secondary-text-color);
    }
}